<template>
  <div id="giftData">
    <common-header :title="'礼物记录'"></common-header>
    <div class="data__list">
      <div class="data" v-for="(item,index) in gifts">
        <div class="data__avatar">
          <img :src="item.imagePath" alt="">
          <div class="data__avatar__info">
            <div class="info__text">{{processType[item.processType]}}</div>
            <div class="info__date">{{item.time}}</div>
          </div>
        </div>
        <div class="data__gift">
          <img :src="item.giftImagePath" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
	export default {
		data(){
			return {
				gifts:[],
				processType:['领取','','','','','转赠','','','','兑换现金'],
				PARAMS:{
					pa:1,
					li:999,
					ob:1,
					ot:1,
				}
			}
		},
		created(){
			this.getGiftLogList()
		},
		methods:{
			getGiftLogList(){
				this.$Api.getGiftLogList(2,this.PARAMS).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.gifts = res.q.gifts
					}
				})
			},
		}
	}
</script>

<style lang="scss">
  #giftData {
    height: 100%;
    background: #f0f0f0;

    .data__list {
      .data {
        display: flex;
        justify-content: space-between;
        padding: 0.267rem 0.4rem;
        height: 1.333rem;
        box-sizing: content-box;
        background: #fff;
        border-bottom: 1px solid #eee;

        .data__avatar {
          display: flex;          
          height: 1.333rem;

          img {
            margin-right: 0.4rem;
            height: 1.333rem;
            width: 1.333rem;
            border-radius: 50%;
          }

          .data__avatar__info {
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            .info__text {
              font-size: 0.4rem;
              color: #333;
            }

            .info__date {
              font-size: 0.3467rem;
              color: #666;
            }
          }
        }

        .data__gift {
          width: 1.333rem;
          height: 1.333rem;

          img {
            height: 100%;
            width: 100%;
            border-radius: 4px;
          }
        }
      }
    }
  }
</style>



